/* Общие стили для контейнера */
.features-container {
  /* max-width: 1480px;
  margin: 0 auto;
  padding: 0 15px; */
}

/* Секция сравнения */
.comparison-section {
  background-color: #0a2540; /* Тёмно-синий фон */
  padding: 120px 138px;
}

.comparison-section__header {
  width: 604px;
}

/* Заголовок */
.comparison-section__title {
  color: var(--White, #fff);
  font-size: 38px;
  font-weight: 600;
  line-height: 120%;
  margin-bottom: 32px;
}

/* Подзаголовок */
.comparison-section__subtitle {
  color: var(--Light-Grey, #adbdcc);
  font-size: 14px;
  font-weight: 400;
  line-height: 155%;
  margin-bottom: 80px;
}

/* Таблица */
.comparison-table {
  max-width: 1240px; /* было 1180px */
  width: 100%;
  border-collapse: separate;
  margin-bottom: 30px;
}

.comparison-table th,
.comparison-table td {
  color: #f6f9fc;
  padding: 15px;
  font-size: 16px;
  font-weight: 500;
  line-height: 150%;
}

.comparison-table__header {
}

.comparison-table__header th {
  margin-right: 8px; /* Промежуток 8px между th */
  text-align: center; /* Центрируем текст */
}

/* Стили для заголовков таблицы */
.single-lp-header {
  background-color: #2395ff; /* Бирюзовый */
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 18px;
  font-weight: 600;
  line-height: 150%;
  margin-right: 8px;
  width: 170px;
}

.prime-header {
  background-color: #32ddb8; /* Зелёный */
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 18px;
  font-weight: 600;
  line-height: 150%;
  margin-right: 8px;
  width: 223px;
}

.several-lps-header {
  background-color: #2395ff; /* Синий */
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 18px;
  font-weight: 600;
  line-height: 150%;
  width: 192px;
}

.features-header {
  color: var(--White, #fff);
  font-size: 18px;
  font-weight: 600;
  line-height: 150%;
  border-radius: 10px;
  background: var(--Dark-Navy, #0c2e4e);
  text-align: left;
  margin-right: 8px;
  width: 571px;
}

/* Стили для галочек и крестиков */
.checkmark {
  font-weight: bold;
  text-align: center;
}

.cross {
  font-weight: bold;
  text-align: center;
}

/* Индикатор риска */
.risk-indicator {
  display: flex;
  justify-content: center;
  gap: 0;
}

.risk-indicator__item {
  color: #f6f9fc;
  padding: 10px 20px;
  font-size: 16px;
  text-align: center;
}

.comparison-table__row td {
  border-bottom: 1px solid #c9c2; /* Светло-синий разделитель */
}

/* Убираем разделитель для последней строки */
.comparison-table__row:last-child td {
  border-bottom: none;
}

.risk-high {
  color: #f6f9fc;
}

.risk-medium {
  color: #f6f9fc;
}

.risk-low {
  color: #f6f9fc;
}

.icon {
  width: 18px;
  height: 36px;
}
