/* Общие стили для контейнера */
.container-heder {
  /* max-width: 1440px; */
  background: var(--Background, #f6f9fc);
}

.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 100px;
  flex-shrink: 0;
}

.header__nav-wrap {
  display: flex;
  align-items: center;
}

/* Логотип */
.header__logo img {
  height: 60px;
  width: 140px;
}

.logo-text {
  font-size: 24px;
  font-weight: 700;
  color: #1a3c5e;
  text-decoration: none;
}

/* Навигация */
.header__nav {
  /* margin-left: 40px; */
}

.nav__list {
  display: flex;
  list-style: none;
  gap: 56px;
}

.nav__item {
  /* margin-right: 24px; */
}

.nav__link {
  font-size: 16px;
  color: black; /* Темный цвет текста */
  text-decoration: none;
  font-weight: 500;
}

.nav__link:hover {
  color: #00c4b4; /* Цвет при наведении (b2green500) */
}

/* Кнопка */
.btn {
  display: inline-block;
  padding: 12px 36px;
  font-size: 13px;
  font-weight: 500;
  text-decoration: none;
  border-radius: 8px;
  margin-left: 56px;
}

.btn--primary {
  background-color: #00c4b4; /* b2green500 */
  color: #fff;
}

.btn--primary:hover {
  background-color: #00a699; /* Чуть темнее при наведении */
}
