/* Секция Cutting-edge infrastructure */
.infrastructure-section {
  background-color: #f6f9fc; /* Светло-синий фон */
  padding: 120px 100px; /* Отступы сверху/снизу 120px, по бокам 100px */
}

/* Контейнер */
.infrastructure-container {
  max-width: 1440px; /* Максимальная ширина контейнера */
  margin: 0 auto; /* Центрируем */
}

/* Заголовок */
.infrastructure-section__header {
  text-align: center; /* Выравниваем по центру */
  max-width: 604px; /* Ограничиваем ширину заголовка */
  margin: 0 auto 80px; /* Центрируем и отступ до блоков */
}

.infrastructure-section__title {
  color: var(--Dark, #0a2540);
  font-size: 38px;
  font-weight: 600;
  line-height: 120%;
  margin-bottom: 32px;
}

/* Подзаголовки и ссылки Learn More */
.infrastructure-section__subtext {
  margin-bottom: 45px;
  margin-top: 45px; /* Расстояние между подзаголовками */
}

.infrastructure-section__subtitle {
  color: var(--Dark, #0a2540);
  font-size: 24px;
  font-weight: 600;
  line-height: 140%; /* 33.6px */
  margin-bottom: 24px; /* Расстояние до ссылки */
}

.infrastructure-section__subtitle-p {
  color: var(--Grey, #6d849c);
  font-size: 14px;
  font-weight: 400;
  line-height: 155%;
  margin-bottom: 20px;
}

.infrastructure-section__learn-more {
  color: var(--b2green500, #21c5a2);
  font-size: 13px;
  font-weight: 500;
  text-decoration: none; /* Убираем подчёркивание по умолчанию */
  transition: color 0.3s ease; /* Плавный переход */
}

.infrastructure-section__learn-more:hover {
  color: #1a73e8; /* Более тёмный синий при наведении */
  text-decoration: underline; /* Подчёркивание при наведении */
}

/* Контейнер для статистики и карты */
.infrastructure-content {
  display: flex;
  justify-content: center; /* Центрируем блоки */
  align-items: flex-start; /* Выравниваем по верхнему краю */
  gap: 64px; /* Промежуток между блоками */
  flex-direction: column;
}

/* Блок статистики */
.infrastructure-statistic {
  width: 602px; /* Ширина блока */
  height: 288px; /* Высота блока */
  background-color: #f4e4e9; /* Розовый фон */
  flex-shrink: 0; /* Запрещаем сжатие */
  border-radius: 19px; /* Закругление углов */
  background: var(--White, #fff); /* Белый фон */
  box-shadow: 14px 16px 25px 0px rgba(50, 50, 93, 0.08); /* Тень */
  padding: 40px;
}

/* Блок карты */
.infrastructure-map {
  width: 602px; /* Ширина блока */
  height: 288px; /* Высота блока */
  background-color: #f4e4e9; /* Розовый фон */
  flex-shrink: 0; /* Запрещаем сжатие */
  border-radius: 19px; /* Закругление углов */
  background: var(--White, #fff); /* Белый фон */
  box-shadow: 14px 16px 25px 0px rgba(50, 50, 93, 0.08); /* Тень */
  padding: 40px;
}

.infrastructure-image2 {
  width: 522px;
  height: 208px;
}

.infrastructure-section__content {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 140px;
}

.infrastructure-section__label {
  color: var(--b2green500, #21c5a2);
  font-size: 14px;
  font-weight: 500;
  line-height: 150%;
}

.infrastructure-section__text {
  display: flex;
  align-items: center;
  gap: 64px;
  flex-direction: column;
  justify-content: center;
}

.icon-akar {
  width: 7px;
  height: 7px;
}
